.footer
{
    font-family: 'Effra';
    font-size: 12px;

    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
}

.light .footer, .light .footer a{color:#ffffff}

.footer a {
    font-size: 14px;
    margin-right: 25px ;
}

@media screen and (max-width: 500px) {
    .footer a {margin-right: 0;}
    .footer span {
        display: block;
        margin-top: 4px;
    }
}