.emotion-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: auto;
    height: 100%;
    text-align: center;
}

.light .emotion-container {color: #ffffff}

.emotion-container .title {
    position: relative;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
}

.emotion-container h1 {font-family: 'Effra-Heavy'; font-size: 200px; line-height: 1em; margin: 0; font-weight: normal; display:inline;}
.emotion-container h2 {font-family: 'Effra-Bold'; font-size: 58px; line-height: 1em; margin: 0; font-weight: normal;}
.emotion-container h3 {font-family: 'KaushanScript'; font-size: 69px; line-height: 1em; margin: 0; font-weight: normal; white-space: nowrap;}
.emotion-container h3 i{font-size: 45px;}

.emotion-container .links {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;

}

.emotion-container .e-chip.share {
    display: none;
}

@media screen and (max-width: 800px) {
    .emotion-container{width: 95%;}

    .emotion-container .links {
        width: 100%;
        white-space: normal;
    }

    .emotion-container .e-chip.share {
        display: inline-block;
    }
}

@media screen and (max-width: 500px) {
    .emotion-container h3 {font-size: 49px;}
    .emotion-container h3 i{font-size: 30px;}
}