.e-chip{
    display: inline-block;
    padding: 0 15px;
    margin: 0 5px 14px 5px;
    
    background-color: #ffffff;
    border-radius: 19px;

    font-family: 'Effra-Bold';
    color: #494949;
    line-height: 38px;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
}

.e-chip i{
    font-style: normal;
}